<template>
  <div class="musicList">
    <ul>
      <li v-for="(music,index) in musicList" :key="index"
          @click="()=>{clickMusic(index)}"
          class="musicInfo">
        <van-image
          width="0.4rem"
          height="0.4rem"
          radius="0.1rem"
          :src="music.musicImg"
        />
        <div class="info">
          <h1 class="musicName">{{music.musicName}}</h1>
          <p class="musicSinger">{{music.musicSinger}}</p>
        </div>
      </li>
    </ul>
  </div>
</template>

<script>

export default {
  name: 'MusicList',
  computed: {
    musicList() {
      return this.$store.state.musicList;
    },
  },
  methods: {
    clickMusic(musicIndex) {
      this.$store.commit('chooseMusic', musicIndex);
    },
  },
};
</script>

